import React from 'react';

export default function SpecInfo({data}) {
  return (
    <div className="accordItemContent">
      <table className="tableData" style={{width:"100%"}}>
          <tbody>  
            {data && data.map((el)=>(
              <tr>
                <td className="bold"> {el.specName} </td> 
                <td> {el.value} </td> 
              </tr>
            ))}
          </tbody>
      </table>
    </div>
  )
}
